<template>
  <div class="index" v-if="data">
    <header>
      <van-uploader :after-read="afterRead" v-if="share !=1 && !uid">
        <img :src="data.cover_image" />
      </van-uploader>
      <img :src="data.cover_image" v-if="share ==1 || uid" />
      <div class="icon-left-box" @click="handleGoBack" v-if="share != 1">
        <!-- <img class="icon-left"  src="../../assets/images/left.png" > -->
        <van-icon name="arrow-left" style="font-size:18px" color="#ffffff" />
      </div>
    </header>
    <section class="content position_top_40">
      <div class="avatar-box">
        <img :src="data.avatar" />
        <img class="isvip" src="../../assets/images/icon_isvip.png" v-if="data.is_ident_icon == 1" />
      </div>
      <div class="info flex justify-space-between">
        <div class="left">
          <p class="username" @click="handleToeditUsername">{{data.username}}</p>
          <div class="flex flex-none">
            <p class="level" v-bind:class="{'isCarvip':data.is_carvip == 1}">{{data.level}}</p>
            <p class="extcredits flex align-items-center justify_content-center">
              <img class="label_heat_img" src="../../assets/images/icon_label.png" alt />
              {{data.extcredits2}}°
            </p>
          </div>
        </div>
        <div class="right" v-if="share !=1 ">
          <div class="edit" @click="handleToeditUserinfo" v-if="!uid">编辑资料</div>
          <div class="friend" @click="handleToMyFriendlist" v-if="!uid"></div>
          <!-- <router-link to="/my/friendlist" tag="div" class="friend"> 
                      
          </router-link>-->

          <div class="share" @click="handleShare"></div>
        </div>
      </div>
      <p class="bio" @click="handleToEditsignature">{{data.bio? data.bio : '填写个人签名更容易获得别人关注哦~'}}</p>
      <p class="label">
        <span class="gender">
          <img v-if="data.gender == '男'" class="male" src="../../assets/images/male.png" alt />
          <img v-if="data.gender == '女'" class="male" src="../../assets/images/female.png" alt />
          {{data.gender}}
        </span>
        <span class="residecity" v-if="data.residecity">{{data.residecity}}</span>
        <!-- <span class="add-label">+增加标签</span> -->
      </p>
      <div class="attest" v-if="tabs == 4 && appleType != 0 " @click="handleAttest">认证</div>
      <div class="flex count">
        <div>
          <p class="item">获赞</p>
          <p class="value" @click="handleToPage(1)">{{data.zan}}</p>
        </div>
        <div>
          <p class="item">被踩</p>
          <p class="value" @click="handleToPage(2)">{{data.cai}}</p>
        </div>
        <div>
          <p class="item">关注</p>
          <p class="value" @click="handleToPage(3)">{{data.following}}</p>
        </div>
        <div>
          <p class="item">粉丝</p>
          <p class="value" @click="handleToPage(4)">{{data.follower}}</p>
        </div>
      </div>
    </section>
    <van-divider class="position_top_40" />
    <div class="tabs position_top_40">
      <ul class="flex">
        <li v-bind:class="{'active':tabs == 3}" @click="handleTabs(3)">广场</li>
        <li v-bind:class="{'active':tabs == 2}" @click="handleTabs(2)">问答</li>
        <li v-bind:class="{'active':tabs == 1}" @click="handleTabs(1)">投票</li>
        <li v-bind:class="{'active':tabs == 4}" @click="handleTabs(4)" v-if="share !=1 && !uid ">更多</li>
      </ul>
    </div>
    <div class="position_top_40">
      <router-view :uid="data.uid" :type="tabs"></router-view>
    </div>
    <Share :shareShow="shareShow" @handleShareEvent="handleShareEvent"></Share>
  </div>
</template>

<script>
import { Divider } from "vant";
import { Toast } from "vant";
import Share from "@c/common/share";
export default {
  components: {
    Share
  },
  data() {
    return {
      uid: this.$route.query.uid ? this.$route.query.uid : "",
      shareShow: false,
      share: this.$route.query.share, // 1 : 分享出去的页面
      data: "",
      appleType: 0, // 认证类型
      tabs: this.$route.query.tabs ? this.$route.query.tabs : 3
    };
  },
  created() {
    document.title = "我的";
    if (this.$route.query.share == 1) {
      document.title = "E区新能源车主壹社区";
      Toast.loading({
        message: "加载中...",
        forbidClick: true
      });
      setTimeout(() => {
        this.getUserInfo();
        this.getApplyType();
      }, 800);
    } else {
      if (this.$store.state.userData) {
        this.getUserInfo();
        this.getApplyType();
      } else {
        let frame = document.createElement("frame");
        frame.src = "teldapp://login";
        document.documentElement.childNodes[0].appendChild(frame);
        // window.location.href= "teldapp://login"
      }
    }
  },
  mounted() {
    this.$nextTick(() => {});
  },
  methods: {
    handleTabs(val) {
      this.tabs = val;

      if (val == 3) {
        this.$router.push({ path: "/my/index/square", query: { tabs: val } });
      } else if (val == 2) {
        this.$router.push({ path: "/my/index/answers", query: { tabs: val } });
      } else if (val == 1) {
        this.$router.push({ path: "/my/index/vote", query: { tabs: val } });
      } else if (val == 4) {
        this.$router.push({ path: "/my/index/more", query: { tabs: val } });
      }
    },
    afterRead(file) {
      this.addImageOfUserCover(file.file);
    },
    // 点击认证按钮
    handleAttest() {
      let url,
        json = { url: url, share: "0", keyboard: "0" };
      switch (this.appleType) {
        case 1:
          url = `${this.$store.state.indexUrl +
            `my/mycertification?` +
            this.$store.state.appParams}`;
          //  this.$router.push({ path: `/my/mycertification` })
          break;
        case 2:
          url = `${this.$store.state.indexUrl +
            `my/verifyformZj?` +
            this.$store.state.appParams}`;
          //  this.$router.push({ path: `/my/mycertification` })
          break;
        case 3:
          url = `${this.$store.state.indexUrl +
            `my/verifyformMt?` +
            this.$store.state.appParams}`;
          //  this.$router.push({ path: `/my/mycertification` })
          break;
      }
      window.location.href =
        `teldapp://newWebview?params=` + JSON.stringify(json);
    },
    // 点击分享按钮
    handleShare() {
      let url = `${this.$store.state.indexUrl +
          "my/index/square?share=1" +
          "&" +
          this.$store.state.appParams}`,
        json = {
          url: url,
          channel: "wxp,wxf,sina,qqp,qqz",
          title: `【E区】${this.data.username}`,
          desc: this.data.bio,
          image: this.data.avatar
        };

      window.location.href = `teldapp://share?params=` + JSON.stringify(json);
      // this.shareShow = true;
    },
    // 分享组件返回的函数
    handleShareEvent(v) {
      this.shareShow = v;
    },
    // 获取认证类别
    getApplyType() {
      this.api
        .getApplyType({
          token: this.$store.state.userData
        })
        .then(res => {
          if (res.err_code == 0) {
            this.appleType = res.data.type;
          } else {
          }
        });
    },
    // 上传用户封面图
    addImageOfUserCover(file) {
      Toast.loading({
        message: "加载中...",
        forbidClick: true
      });
      let formData = new FormData();
      formData.append("token", this.$store.state.userData);
      formData.append("myfile", file);
      this.api
        .addImageOfUserCover(formData)
        .then(res => {
          Toast.clear();
          if (res.err_code == 0) {
            this.getUserInfo();
          } else {
          }
        })
        .catch(err => {
          Toast(err);
        });
    },
    // 编辑签名
    handleToEditsignature() {
      let url = `${this.$store.state.indexUrl +
          "my/editsignature?" +
          this.$store.state.appParams}`,
        json = { url: url, share: "0", keyboard: "0" };
      if (this.share != 1 && !this.uid) {
        window.location.href =
          `teldapp://newWebview?params=` + JSON.stringify(json);
        // this.$router.push({ path: '/my/editsignature' })
      }
    },
    // 编辑昵称
    handleToeditUsername() {
      return null;
      if (this.share != 1 && !this.uid) {
        this.$router.push({ path: "/my/editusername" });
      }
    },
    // 调转我的好友页面
    handleToMyFriendlist() {
      let url = `${this.$store.state.indexUrl +
          "my/friendlist?" +
          this.$store.state.appParams}`,
        json = { url: url, share: "0", keyboard: "0" };
      if (!this.$store.state.userData) {
        let frame = document.createElement("frame");
        frame.src = "teldapp://login";
        document.documentElement.childNodes[0].appendChild(frame);
        //  window.location.href= "teldapp://login"
      } else {
        window.location.href =
          `teldapp://newWebview?params=` + JSON.stringify(json);
      }
    },
    // 编辑资料
    handleToeditUserinfo() {
      let url = `${this.$store.state.indexUrl +
          "my/edituserinfo?" +
          this.$store.state.appParams}`,
        json = { url: url, share: "0", keyboard: "0" };
      if (!this.$store.state.userData) {
        //  window.location.href= "teldapp://login"
        let frame = document.createElement("frame");
        frame.src = "teldapp://login";
        document.documentElement.childNodes[0].appendChild(frame);
      } else {
        window.location.href =
          `teldapp://newWebview?params=` + JSON.stringify(json);
      }
      // this.$router.push({ path: '/my/edituserinfo' })
    },
    // 添加页面
    handleToPage(v) {
      if (this.share == 1) {
        return null;
      }
      let url, json;

      switch (v) {
        case 1:
          break;
        case 2:
          break;
        case 3:
          url = `${this.$store.state.indexUrl +
            "my/attention?type=1&user=1&uid=" +
            this.uid +
            "&" +
            this.$store.state.appParams}`;
          json = { url: url, share: "0", keyboard: "0" };
          window.location.href =
            `teldapp://newWebview?params=` + JSON.stringify(json);
          console.log(`teldapp://newWebview?params=` + JSON.stringify(json));
          //this.$router.push({ path: '/my/attention' ,query:{type:1,user:1,uid:this.uid}})
          break;
        case 4:
          url = `${this.$store.state.indexUrl +
            "my/attention?type=2&user=1&uid=" +
            this.uid +
            "&" +
            this.$store.state.appParams}`;
          json = { url: url, share: "0", keyboard: "0" };
          window.location.href =
            `teldapp://newWebview?params=` + JSON.stringify(json);
          //this.$router.push({ path: '/my/attention' ,query:{type:2,user:1,uid:this.uid}})
          break;
      }
    },
    handleGoBack() {
      this.$router.push({ path: "/" });
    },
    // 获取 用户信息
    getUserInfo() {
      Toast.loading({
        message: "加载中...",
        forbidClick: true
      });
      this.api
        .getUserInfo({
          token: this.$store.state.userData,
          uid: this.uid ? this.uid : ""
        })
        .then(res => {
          Toast.clear();
          if (res.err_code == 0) {
            this.data = res.data;
          }
        });
    }
  }
};
</script>

